.imgSize
{
	width: 100%;
	height: auto;
}

#slideshow 
{
	position: absolute;
    top: 45%;
    left: 48%;
	transform: translate(-50%, -50%);
	width: 340px;
	height: 400px;
	padding: 10px;
}
  
#slideshow > div 
{
	position: absolute;
	top: 10px;
	left: 10px;
	right: 10px;
	bottom: 10px;
}

