.display-photo
{
	position: absolute;
	width: 100%;
	height: 100%;
	top: 100%;
	left: 0;
	overflow: hidden;
	z-index: -2;
	animation: photoScale 1s forwards;
}

li:hover .display-photo
{
	animation: photoScaleXL 1s forwards;
}

@keyframes photoScale
{ 
  0% {transform: scale(1);}
  100% {transform: scale(0);}
}

@keyframes photoScaleXL
{ 
  0% {transform: scale(0);}
  100% {transform: scale(1);}
}